/*
  COLORS
  WHITE : #FEFCF3
  PURPLE : #9688B7
  LIGHT BLUE : #496899
  MIDDLE BLUE : #254470
  DARK BLUE : #0B2540

*/


.demo {
  min-height: 93vh;
  background-color: #0B2540;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.demo h2 {
  color: #FEFCF3;
  margin: 2em 0.5em 0 0.5em;
  padding-top: 3em;
  font-size: 2.5em;
}

.demo_row1 {
  padding-top: 2%;
}

.demo_col {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FEFCF3;
  min-height: 25em;
  margin: 2em auto;
  border-radius: 20px;
  padding: 2em 0;
  width: 80%;
}

.demo_col:hover {
  background-color: #FEFCF3;
}

.demo button {
  font-size: 1.3em;
  padding: .5em 1em;
  transition: 1s;
  border: none;
  border-radius: 5px;
  height: fit-content;
  width: 80%;
  background-color: #0B2540;
  color: #FEFCF3;
}

.demo button:hover {
  transform: scale(1.1);
}

@media screen and (max-width: 550px){
  .demo h2 {
    font-size: 1.5em;
  }
}

@media screen and (min-width: 550px){
  .demo h2 {
    margin-top: 0;
  }
}