.signup {
  width: 100%;
  height: 100vh;
  background-color: #254470; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.signup {
  display: flex;
  justify-content: center;
  align-items: center;
}

.signup-wrapper {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  border-radius: 10px;
  padding: 1em;
  display: flex;
  justify-content: center;
}

.signup h2 {
  color: white;
  margin-bottom: 1em;
  font-size: 2.5em;
  font-weight: bold;
}

.signup_input {
  margin: 1em auto;
  height: 3em;
  width: 90%;
  border-radius: 10px;
  border: none;
  background-color: #fefcf3;
  padding: 1em;
  outline: none;
}

.signup_button {
  margin: 1em auto;
  height: 3em;
  width: 90%;
  border-radius: 10px;
  border: none;
  background-color: #fefcf3;
  font-weight: 500;
}

.signup_button:hover {
  background-color: #e6e5e0;
}


.icon {
  color: white;
  cursor: pointer;
  background-color: red;
}

.sucessMessage {
  margin: 0 auto;
  padding: 0.5em;
  color: yellowgreen;
  font-size: 1em;
  border: solid 2px yellowgreen;
  width: fit-content;
  text-align: center;
}

.errorMessage {
  margin: 0 auto;
  padding: 0.5em;
  color: red;
  font-size: 1em;
  border: solid 2px red;
  width: fit-content;
  text-align: center;
}

@media screen and (min-width: 650px) {
  .signup-wrapper {
    width: 60%;
    height: 80%;
  }
  .signup_input {
    width: 70%;
  }
  .signup_button {
    width: 70%;
  }
}

@media screen and (min-width: 1200px) {
  .signup-wrapper {
    width: 50%;
  }
  .signup_input {
    width: 70%;
  }
  .signup_button {
    width: 70%;
  }
}
